<template>
  <div class="detail">
    <div class="nav-bg"></div>
    <van-nav-bar title="商品详情" left-text="返回" @click-left="goMenu" left-arrow :fixed="true" class="detail_title"/>
    <div class="detail-top">
      <img :src="product.large_img" alt />
      <div class="detail-top-text">
        <div class="text-name">{{product.name}}</div>
        <div class="text-type">{{product.enname}}</div>
      </div>
    </div>
    <div class="detail-content">
      <div class="detail-item" v-for="( item , index ) in product.rules" :key="index">
        <div class="item-title">{{item.title}}</div>
        <div class="detail-item-tagbox clearfix">
          <div
            class="detail-item-tag fl"
            v-for="( v , i ) in item.rules"
            :key="i"
            :class="{active:v.isActive}"
            @click="taggerteb(v,index)"
          >{{v.title}}</div>
        </div>
      </div>
      <div class="desc-box">
        <div class="desc-title">商品描述</div>
        <div class="desc-content">
          <div
            class="desc-text"
            v-for="( item , index ) in product.desc"
            :key="index"
          >{{index +1 }}、{{item}}</div>
        </div>
        <div class="clearfix desc-price-count">
          <div class="fl price">￥{{product.price}}</div>
          <div class="fr count">
            <van-stepper v-model="count" theme="round" button-size="24" disable-input />
          </div>
        </div>
      </div>
      <div>
        <!-- like -->
        <van-goods-action>
          <van-goods-action-icon icon="cart-o" text="购物车" :badge="cartbadge" @click="goShopbag" />
          <van-goods-action-icon
            @click="likeAndNotLike"
            :icon=" isopen ? 'like' : 'like-o'"
            :text=" isopen ? '已收藏' : '收藏'"
            color="#ff5000"
          />
          <van-goods-action-button color="#6A91EC" type="warning" text="加入购物车" @click="addhopbag" />
          <van-goods-action-button color="#0C34BA" type="danger" text="立即购买" />
        </van-goods-action>
      </div>
    </div>
  </div>
</template>
  
<script>
import "../assets/less/detail.less";

import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      count: 0,
      pid: "",
      product: {},
      isopen: false,
      cartbadge: 0,
    };
  },
  methods: {
    // 开关
    // isOpen() {
    // this.isopen = !this.isopen;
    // this.findLike();
    // },
    // 根据商品pid获取商品数据
    getProductByPid() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/productDetail",
        // 如果get请求,参数需要放在params,如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          // 

          if (result.data.code == 600) {
            // 处理商品描述数据
            // 以 \n 分割成多段描述
            result.data.result[0].desc = result.data.result[0].desc.split(/\n/);

            // 可添加的物料
            let ruleItems = ["tem", "sugar", "cream", "milk"];
            let rulesData = [];

            ruleItems.map((v) => {
              // 传入商品无此配料时跳过
              if (result.data.result[0][v] == "") {
                return;
              }

              // 将排除后的重新组合成对象
              let rule = {
                title: result.data.result[0][v + "_desc"],
                rules: [],
              };

              // 切割配料内容
              let r = result.data.result[0][v].split("/");
              // 

              r.map((value, index) => {
                let o = {
                  title: value,
                  isActive: index == 0,
                };
                rule.rules.push(o);
              });

              rulesData.push(rule);
              // 
            });
            // 
            result.data.result[0].rules = rulesData;

            this.product = result.data.result[0];
            // 
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
    // 点击切换高亮
    taggerteb(v, index) {
      if (v.isActive) {
        return;
      }
      let ruleData = this.product.rules[index].rules;
      for (let i = 0; i < ruleData.length; i++) {
        if (ruleData[i].isActive) {
          ruleData[i].isActive = false;
          break;
        }
      }
      v.isActive = true;
    },
    // 查询商品是否被收藏
    findLike() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      // 
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          tokenString,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 1000) {
            if (result.data.result.length == 1) {
              this.isopen = true;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    // 收藏商品
    likeAndNotLike() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
      });

      // 判断是收藏还是取消收藏
      let url = this.isopen ? "/notlike" : "/like";

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          this.$toast.clear();
          // 

          if (result.data.code == 700) {
            return this.$router.push({ name: "Login" });
          }

          this.$toast(result.data.msg);

          if (result.data.code == 800) {
            this.isopen = true;
          } else if (result.data.code == 900) {
            this.isopen = false;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },

    //查询购物袋数目
    findShopbag() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 5000) {
            this.cartbadge = result.data.result.length;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 加入购物车
    addhopbag() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      //获取选择的规格
      let rules = this.product.rules;
      // 

      let rule = [];
      //遍历规格,将所选高亮规格提取出来
      rules.map((v) => {
        // 
        for (let i = 0; i < v.rules.length; i++) {
          if (v.rules[i].isActive) {
            rule.push(v.rules[i].title);
            break;
          }
        }
      });

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
        count: this.count,
        rule: rule.join("/"),
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              //累加徽章数量, 购物车数据，而不是购物车商品总数量
              this.cartbadge++;
            }
          }
          // 弹窗提示
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 返回菜丹
    goMenu() {
      
      // this.$router.push("main/menu");
      this.$router.go(-1);
      // this.$router.push(-1);
    },
    //回到购物袋
    goShopbag() {
      this.$router.push({ name: "Shopbag" });
    },
    //提交订单
    // 
  },
  created() {
    // 接收传过来的商品pid
    this.pid = this.$route.query.pid;
    this.getProductByPid();
    //查询商品是否被收藏
    this.findLike();
    // 查询购物车数量
    this.findShopbag();
  },
};
</script>